body {
  font-family: 'Bitter', serif;
}

.container {
  margin-top: 30px;
  max-width: 700px;
}

.nav {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.news-list-container {
  margin-top: 50px;
}

.flex-parent {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  color: #000;
  text-decoration: none;
}
.flex-parent:hover {
  text-decoration: none;
}

.list-group {
  text-decoration: none;
  list-style: none;
}

.list-item {
  padding: 10px;
  font-size: 20px;
  overflow: hidden;
}
.list-item:not(:last-of-type) {
  border-bottom: 1px solid #d6d6d6;
}

.newsArticle-image--wrapper {
  margin: 6px 20px 0 0;
}

.newsArticle-image {
  width: 150px;
  height: 150px;
  background-size: cover;
  background-position: center;
}

@media (max-width: 768px) {
  .container {
    padding: 0;
  }
  .newsArticle-image {
    width: 100px;
    height: 100px;
  }
  .newsArticle-image--wrapper {
    margin-right: 10px;
  }
  .list-item {
    font-size: 16px;
  }
}



